import React from 'react'
import './blog.scss';
import Blogpost from './blogpost';
import Recentpost from './recentpost'
import blog1_img from '../../assets/images/blog/blog_1.webp';
import blog2_img from '../../assets/images/blog/blog_2.webp';
import blog3_img from '../../assets/images/blog/blog_3.webp';

export default function index() {
    const blogposts = [
        {
            title: "NEW BOOK: HOLIDAY SEASON SPECIALS",
            date: "February 15 2020",
            imgSrc: blog1_img,
            shortDescription: "This is your blog post. Use this space to connect with your visitors and potential customers in a way that’s current and interesting. Think of it as an ongoing conversation where you can share updates about business, industry trends, news, and more. Blog posts can be a great way to position yourself as an authority in your field and keep your visitors engaged. To improve your sit..."
        },
        {
            title: "THE FRENCH EXPERIENCE",
            date: "February 17 2020",
            imgSrc: blog2_img,
            shortDescription: "This is your blog post. Use this space to connect with your visitors and potential customers in a way that’s current and interesting. Think of it as an ongoing conversation where you can share updates about business, industry trends, news, and more. Blog posts can be a great way to position yourself as an authority in your field and keep your visitors engaged. To improve your sit..."
        },
        {
            title: "BEHIND THE SCENES OF BREAKFAST DELIGHTS",
            date: "February 20 2020",
            imgSrc: blog3_img,
            shortDescription: "This is your blog post. Use this space to connect with your visitors and potential customers in a way that’s current and interesting. Think of it as an ongoing conversation where you can share updates about business, industry trends, news, and more. Blog posts can be a great way to position yourself as an authority in your field and keep your visitors engaged. To improve your sit..."
        }
    ];
    return (
        <div className="blogpost-container">
            <div className="blogposts">
                {blogposts.map((blogpost, index) => (<Blogpost key={index} blog={blogpost} />))}
            </div>
            <div className="recentposts">
                <h4>Recent Posts</h4>
                <hr/>
                {blogposts.map((blogpost, index) => (<Recentpost key={index} blog={blogpost} />))}
            </div>
        </div>
    )
}
