import React from 'react'

export default function blogpost({blog: {title, date, imgSrc, shortDescription} }) {
    return (
        <div className="blogpost">
            <h4>{title}</h4>
            <h6>{date}</h6>
            <img src={imgSrc} alt="" />
            <p> {shortDescription} </p>
            <button>Read More</button>
        </div>
    )
}
